<style scoped>



</style>

<template>

<div class="jumbotron bg-transparent text-center margin-none">
    <h1></h1>
    <button @click="createMoban()" class="btn btn-primary"><i class="fa fa-plus"></i>  </button>
    <div class="row" data-toggle="isotope">

        </br>
        <div class="item col-md-12 col-xs-12">
            <div class="panel panel-default">
                <table class="table table-striped margin-none">
                    <thead>
                        <tr>
                            <td class="text-left"><strong></strong></td>
                            <td class="text-left"><strong></strong></td>
                            <td><strong></strong></td>
                            <td class="text-right width-200"><strong></strong></td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(t, index) in mobans">
                            <td class="text-left"><strong class="text-muted ">{{index+1}}.</strong>
                                <router-link :to="{ name: 'templatelist', params: {tname:t.Name}}" class="text-primary"><strong>{{t.Name}}</strong></router-link>
                            </td>
                            <td class="text-left">
                                </strong>
                                <a href="#" class="text-primary"><strong>{{t.Potocol}}</strong></a>
                            </td>
                            <td class="text-danger"></td>
                            <td class="text-right">
                                <router-link :to="{ name: 'templatelist', params: {tname:t.Name, tid:t.ID, tchannel:t.Channel, tpotocol:t.Potocol}}" class="btn btn-default btn-xs"></router-link>
                                <button @click="deleteTemplate(t)" class="btn btn-danger btn-xs"></button>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    </div>
    <div v-if="show" class="modal fade slide-down in" style="display: block;">
        <div class="modal-dialog">
            <div class="v-cell">
                <div class="modal-content">
                    <div class="modal-header">
                        <button @click="close" type="button" class="close" data-dismiss="modal"><span aria-hidden="true"></span><span class="sr-only">Close</span></button>
                        <div class="text-center">
                            <h4 class="modal-title"></h4>
                        </div>
                    </div>
                    <div class="modal-body">
                        <form class="form-horizontal" role="form">
                            <div class="form-group">
                                <label for="inputEmail1" class="col-sm-3 control-label"></label>
                                <div class="col-sm-9">
                                    <input class="form-control" id="inputEmail1" placeholder="" type="text" v-model="current.Name">
                                </div>
                            </div>
                            <!-- <div class="form-group">
                                <label for="inputEmail1" class="col-sm-3 control-label"></label>
                                <div class="col-sm-9">
                                    <input class="form-control" id="inputEmail1" placeholder="" type="text" v-model.number="current.ID">
                                </div>
                            </div>
                            <div class="form-group">
                                <label for="inputEmail1" class="col-sm-3 control-label"></label>
                                <div class="col-sm-9">
                                    <input class="form-control" id="inputEmail1" placeholder="" type="text" v-model="current.Channel">
                                </div>
                            </div> -->
                            <div class="form-group">
                                <label for="inputEmail1" class="col-sm-3 control-label"></label>
                                <div class="col-sm-9">
                                    <v-select v-model="current.picker" :options="current.organizeMembers" placeholder=""></v-select>
                                </div>
                            </div>
                            <div class="form-group margin-none">
                                <div class="col-sm-offset-3 col-sm-9">
                                    <button@click="save" type="submit" class="btn btn-primary pull-right"></button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div </div>
            </div>
        </div>
    </div>

</template>

<script>

import vSelect from 'vue-select'
export default {
    name: 'template',
    data() {
        return {
            msg: '',
            show: false,
            mobans: [{
                organizeMembers: [{
                    label: "MODBUS",
                    value: 1
                }, {
                    label: "MODBUSTCP",
                    value: 2
                }],
                picker: null,
                // ID: 0,
                Name: '',
                    // Channel: '',
                Potocol: ''
            }],
            current: {
                organizeMembers: [{
                    label: "MODBUS",
                    value: 1
                }, {
                    label: "MODBUSTCP",
                    value: 2
                }],
                picker: null,
                // ID: 0,
                Name: '',
                    // Channel: '',
                Potocol: ''
            }
        }
    },
    methods: {
        deleteTemplate: function(template) {
          var url = '/templates/' + template.Name
          this.$http.delete(url).then(function(res) {
              if (res.data.error) {
                  alert(res.data.msg)
              } else {
                  this.loadMoban()
              }
          }, function() {
              alert("")
          })
        },
        createMoban: function() { //
            this.current = this.temp()
            this.show = true
                // this.current.state = "create"
        },
        close: function() {
            this.show = false
            this.current = this.temp()
        },
        save: function() {
            console.log(this.current.picker.label);
            this.current.Potocol = this.current.picker.label
            console.log(this.current);
            this.$http.post('/templates', this.current).then(function(res) {
                if (res.data.error) {
                    alert(res.data.Message)
                } else {
                    this.loadMoban()
                    this.show = false
                    this.current = this.temp()
                }
            }, function() {
                alert(" ")
            })
        },
        loadMoban: function() {
            this.$http.get('/templates').then(function(res) {
                if (res.data.error) alert(res.data.msg)
                this.mobans = res.data.data
            }, function() {
                alert("")
            })
        },
        temp: function() {
            return {
                organizeMembers: [{
                    label: "MODBUS",
                    value: 1
                }, {
                    label: "MODBUSTCP",
                    value: 2
                }],
                picker: null,
                // ID: 0,
                Name: '',
                    // Channel: '',
                Potocol: ''
            }
        }
    },
    components: {
        vSelect
    },
    created() {
        this.loadMoban()
    }
}

</script>



